*,
*:before,
*:after {
  padding: 0;
  margin: 0;
  outline: 0px solid;
  box-sizing: inherit;
}
*:hover, *:active, *:focus, *:visited,
*:before:hover,
*:before:active,
*:before:focus,
*:before:visited,
*:after:hover,
*:after:active,
*:after:focus,
*:after:visited {
  outline: 0px solid;
}

html {
  font-size: 62.5%;
  box-sizing: border-box;
}

body {
  font-family: sans-serif;
  font-size: 1.6rem;
  font-weight: 400;
  font-style: normal;
}

ul {
  list-style-type: none;
  padding: 0;
  margin: 0;
}

img {
  display: block;
  width: auto;
  height: auto;
  max-width: 100%;
}

h1, h2, h3, h4, h5, h6 {
  margin: 0;
  padding: 0;
  text-transform: none;
  color: #000000;
}

p {
  font-family: inherit;
  font-weight: inherit;
  color: inherit;
  padding: 0;
  margin: 0;
  font-size: inherit;
  line-height: inherit;
}

a {
  display: inline-block;
  width: auto;
  color: inherit;
  cursor: pointer;
  transition: all 0.4s ease;
  text-decoration: none;
}

.o-container {
  display: block;
  width: 100%;
  height: auto;
  margin: 0 auto;
}

.o-col-100 {
  width: 100%;
  padding: 2rem;
}
.o-col-80 {
  width: 80%;
  padding: 2rem;
}
.o-col-50 {
  width: 50%;
  padding: 2rem;
}

o-font-h1 {
  color: black;
}
o-font-h2 {
  color: black;
}
o-font-h3 {
  color: black;
}
o-font-h4 {
  color: black;
}
o-font-h5 {
  color: black;
}
o-font-h6 {
  color: black;
}
o-font-p {
  color: #5c66c0;
}
o-font-p--mod {
  color: #fff;
}

.o-bg-gradient {
  background: rgb(0, 95, 121);
  background: linear-gradient(180deg, rgb(0, 95, 121) 0%, rgba(51, 107, 122, 0.7147233894) 71%);
}

.c-header {
  display: block;
}
.c-header__container {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: center;
  background-color: #757575;
  padding: 2rem 0;
}
.c-header__logo {
  display: block;
  height: auto;
}
.c-header__nav {
  padding: 1rem 0;
}
.c-header__nav-link {
  display: inline-block;
  padding: 0 1.5rem;
  color: #ffffff;
}
.c-header__nav-link:hover {
  color: #3434e4;
}

.c-emisora {
  display: block;
}
.c-emisora__container {
  padding: 5rem 0;
}
.c-emisora__container2 {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  column-gap: 1.5rem;
  padding: 1.5rem;
  border-radius: 1.5rem;
  background-color: rgba(46, 43, 43, 0.8705882353);
}
.c-emisora__online {
  grid-column: span 2;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  flex-wrap: nowrap;
}
.c-emisora__direct {
  display: flex;
  justify-content: space-around;
  align-items: center;
  margin: auto 0;
}
.c-emisora__direct-picture {
  padding: 1rem;
  max-width: 20rem;
}
.c-emisora__direct-picture-img {
  border-radius: 1.5rem;
}
.c-emisora__direct-text {
  padding: 1rem;
  color: #ffffff;
}
.c-emisora__direct-audio {
  display: flex;
  justify-content: center;
  align-items: center;
}
.c-emisora__listTop {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}
.c-emisora__listTop-tittle {
  text-align: center;
  font-size: 2rem;
  font-weight: bold;
  margin: 0.5rem;
  color: #5c66c0;
}
.c-emisora__listTop-top5 {
  padding: 1rem;
  width: 100%;
}
.c-emisora__listTop-top5-item {
  display: flex;
  justify-content: space-between;
  align-items: center;
  font-size: 2rem;
  padding-top: 2rem;
  border-bottom: 0.2rem dotted #757575;
}
.c-emisora__listTop-p {
  color: #5c66c0;
}
.c-emisora__listTop-p--mod {
  color: #ffffff;
  text-transform: lowercase;
  font-size: 1.5rem;
}

.c-list {
  display: block;
}
.c-list__container {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}
.c-list__h1 {
  text-align: center;
  border-top: 1px dashed #010101;
  border-bottom: 1px dashed #010101;
}
.c-list__grid {
  display: grid;
  grid-template-columns: repeat(7, 1fr);
  grid-gap: 1.5rem;
  border: 1px solid #000;
  border-radius: 2rem;
  margin: 2rem;
  background-color: #ffffff;
}
.c-list__grid-item {
  border-radius: 1rem;
  margin: auto;
}
.c-list__grid-item img {
  margin: auto;
  border-radius: 1rem;
  max-height: 10rem;
  cursor: pointer;
}
.c-list__grid-item img:hover {
  border: 1px solid #000;
  box-shadow: 2px 2px 7px rgba(0, 0, 0, 0.6);
}
.c-list__p {
  padding: 2rem;
}
.c-list__p p {
  padding: 0;
}

.c-footer {
  display: block;
}
.c-footer__container {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: center;
  padding: 2rem 0;
  background-color: #1d1818;
}
.c-footer__email {
  padding-bottom: 1rem;
}
.c-footer__email-link {
  color: #ffffff;
}
.c-footer__email-link:hover {
  color: #3434e4;
}
.c-footer__social {
  padding-top: 1rem;
}
.c-footer__social-link {
  padding: 0 1rem;
  color: #ffffff;
}
.c-footer .fa-facebook-f:hover {
  color: #3b5998;
}
.c-footer .fa-twitter:hover {
  color: #1da1f2;
}
.c-footer .fa-instagram:hover {
  color: #3f729b;
}
.c-footer .fa-youtube:hover {
  color: #cd201f;
}

u-none {
  display: none !important;
}